import React from 'react';
import ReactDOM, { render } from 'react-dom';
import './index.css';

//Responsible for rendering the board
class RenderBoard extends React.Component {
  //Constructor function 
  constructor(props) {
    super(props);
    //Sets the states:
      //buttonState is a 2D array that is initially empty
      //Will later contain a "R" or a "Y" to indicate the colour
      //checkTurn will be used to check whether it is red or yellows turn
      //gameover will be true if there is a winner or 
    this.state = {
      'buttonState': [
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', '']],
      'checkTurn': '',
      'gameover': false,
    }
    this.handleClick = this.handleClick.bind(this);
  }
  //checks for a winner of the given colour
  checkForWinner(clr) {
    for (let x = 0; x < 5; x++)
      for (let y = 0; y < 7; y++) {
        //check for vertical line of same colour
        if (x < 2 && this.state.buttonState[x][y] === clr && this.state.buttonState[x + 1][y] === clr && this.state.buttonState[x + 2][y] === clr && this.state.buttonState[x + 3][y] === clr)
          return true;
        //check for horizontal line of same colour
        else if (y < 4 && this.state.buttonState[x][y] === clr && this.state.buttonState[x][y + 1] === clr && this.state.buttonState[x][y + 2] === clr && this.state.buttonState[x][y + 3] === clr)
          return true;
        //check for diagonal line for the same colour
        else if (x < 2 && y < 4 && this.state.buttonState[x][y] === clr && this.state.buttonState[x + 1][y + 1] === clr && this.state.buttonState[x + 2][y + 2] === clr && this.state.buttonState[x + 3][y + 3] === clr)
          return true;
        else if (x > 2 && y > 4 && this.state.buttonState[x][y] === clr && this.state.buttonState[x - 1][y - 1] === clr && this.state.buttonState[x - 2][y - 2] === clr && this.state.buttonState[x - 3][y - 3] === clr)
          return true;
        else if (x < 2 && y > 4 && this.state.buttonState[x][y] === clr && this.state.buttonState[x + 1][y - 1] === clr && this.state.buttonState[x + 2][y - 2] === clr && this.state.buttonState[x + 3][y - 3] === clr)
          return true;
        else if (x > 2 && y < 4 && this.state.buttonState[x][y] === clr && this.state.buttonState[x - 1][y + 1] === clr && this.state.buttonState[x - 2][y + 2] === clr && this.state.buttonState[x - 3][y + 3] === clr)
          return true;
      }
  }
  //checks if the board is full
  checkForFullBoard() {
    let full = 0
    for (let x = 0; x < 5; x++) {
      for (let y = 0; y < 7; y++) {
        if (this.state.buttonState[x][y] !== '')
          full++;
      }
    }
    if (full === 35)
      this.gameover = true;
    return this.gameover;
  }

  //clears the board
  clearBoard() {
    let newArray = Array.from(this.state.buttonState);
    for (let x = 0; x < 5; x++) {
      for (let y = 0; y < 7; y++) {
        newArray[x][y] = '';
        this.setState({
          'buttonState': newArray
        });
        document.getElementById((x + 1) + "-" + (y + 1)).style.background = '#FFFFFF';
      }
    }
  }

  //Handles the click event
  handleClick(event) {
    let newArray = Array.from(this.state.buttonState);
    const id = event.target.id;
    var idSplit = id.split("-");
    if (this.state.buttonState[idSplit[0] - 1][idSplit[1] - 1] === '') {
      this.checkTurn += " ";
    }
    //If its yellows turn let them change a free tile 
    if (this.checkTurn.length % 2 === 0 && this.state.buttonState[idSplit[0] - 1][idSplit[1] - 1] !== 'R') {
      newArray[idSplit[0] - 1][idSplit[1] - 1] = 'Y';
      this.setState({ buttonState: newArray });
      document.getElementById(id).style.background = '#FFFF00';
    }
    //If its reds turn let them change a free tile 
    else if (this.checkTurn.length % 2 === 1 && this.state.buttonState[idSplit[0] - 1][idSplit[1] - 1] !== 'Y') {
      newArray[idSplit[0] - 1][idSplit[1] - 1] = 'R';
      this.setState({ buttonState: newArray });
      document.getElementById(id).style.background = '#DC143C';
    }
    //checks for a winner or if the board is full 
    if (this.checkForWinner('R') === true) {
      let self = this;
      setTimeout(function () {
        alert('Red wins!');
      }, 100)
      setTimeout(function () { self.clearBoard(); }, 200);

    }
    if (this.checkForWinner('Y') === true) {
      let self = this;
      setTimeout(function () {
        alert('Yellow wins!');
      }, 100)
      setTimeout(function () { self.clearBoard(); }, 200);
    }
    if (this.checkForFullBoard() === true) {
      this.clearBoard();
      alert("The game is over!");
      this.gameover = false;
    }
  }
  //renders the board containing all the buttons and title
  render() {
    return (
      <div align="center">
        <div>
          <h1>Connect Four!</h1>
        </div>
        <div>
          <button id="1-1" onClick={this.handleClick} />
          <button id="1-2" onClick={this.handleClick} />
          <button id="1-3" onClick={this.handleClick} />
          <button id="1-4" onClick={this.handleClick} />
          <button id="1-5" onClick={this.handleClick} />
          <button id="1-6" onClick={this.handleClick} />
          <button id="1-7" onClick={this.handleClick} />
        </div>
        <div>
          <button id="2-1" onClick={this.handleClick} />
          <button id="2-2" onClick={this.handleClick} />
          <button id="2-3" onClick={this.handleClick} />
          <button id="2-4" onClick={this.handleClick} />
          <button id="2-5" onClick={this.handleClick} />
          <button id="2-6" onClick={this.handleClick} />
          <button id="2-7" onClick={this.handleClick} />
        </div>
        <div>
          <button id="3-1" onClick={this.handleClick} />
          <button id="3-2" onClick={this.handleClick} />
          <button id="3-3" onClick={this.handleClick} />
          <button id="3-4" onClick={this.handleClick} />
          <button id="3-5" onClick={this.handleClick} />
          <button id="3-6" onClick={this.handleClick} />
          <button id="3-7" onClick={this.handleClick} />
        </div>
        <div>
          <button id="4-1" onClick={this.handleClick} />
          <button id="4-2" onClick={this.handleClick} />
          <button id="4-3" onClick={this.handleClick} />
          <button id="4-4" onClick={this.handleClick} />
          <button id="4-5" onClick={this.handleClick} />
          <button id="4-6" onClick={this.handleClick} />
          <button id="4-7" onClick={this.handleClick} />
        </div>
        <div>
          <button id="5-1" onClick={this.handleClick} />
          <button id="5-2" onClick={this.handleClick} />
          <button id="5-3" onClick={this.handleClick} />
          <button id="5-4" onClick={this.handleClick} />
          <button id="5-5" onClick={this.handleClick} />
          <button id="5-6" onClick={this.handleClick} />
          <button id="5-7" onClick={this.handleClick} />
        </div>
      </div>
    );
  }
}


export default RenderBoard;
